html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro";
}

h1 {
  font-size: 2.25rem;
  font-weight: 300;
}
@media (min-width: 40em) {
  h1 {
    font-size: 3.5rem;
  }
}

h2 {
  font-size: 1.3125rem;
}
@media (min-width: 40em) {
  h2 {
    font-size: 1.75rem;
  }
}

h3 {
  font-size: 1rem;
}
@media (min-width: 40em) {
  h3 {
    font-size: 1.4375rem;
  }
}

body {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
}
@media (min-width: 40em) {
  body {
    display: flex;
    flex-direction: row;
  }
}

.title {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
@media (min-width: 40em) {
  .title {
    width: 58.75%;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
}

.main {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0 1.25rem;
}
@media (min-width: 40em) {
  .main {
    width: 41.25%;
    align-items: center;
    justify-content: center;
  }
}

form {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.title {
  text-align: center;
  background: url(images/dark-ribs.webp) no-repeat;
  background-color: #404040;
  background-size: cover;
  color: #f18119;
  font-size: 2.25rem;
  padding: 0 0.625rem 0.3125rem;
}
.title__small {
  font-size: 0.625rem;
  text-transform: uppercase;
  letter-spacing: 0.09375rem;
  font-weight: 900;
}
.title__small::before {
  content: "";
  width: 100%;
  height: 0.4375rem;
  background: transparent linear-gradient(270deg, #FF895D 0%, #DF4E13 0%, #ffa51d 100%) 0% 0% no-repeat padding-box;
  display: block;
  margin-bottom: 0.375rem;
}
@media (min-width: 40em) {
  .title__small {
    position: absolute;
    top: 0;
  }
}
.title__main {
  width: 81.5%;
  color: #fff;
}
.title__main--bold {
  font-weight: 900;
}
.title__subtitle {
  text-transform: uppercase;
  margin-bottom: 0.3125rem;
}

.main {
  color: #404040;
}
.main__title {
  margin-bottom: 0.625rem;
  margin-top: 1.875rem;
}
@media (max-width: 39.9375em) {
  .main__title {
    margin-bottom: 0;
  }
}
@media (min-width: 40em) {
  .main__title {
    margin-top: 0;
  }
}
.main__subtitle {
  color: #f18119;
  text-transform: uppercase;
  margin-top: 0;
}
.main__text {
  font-size: 1.125rem;
  color: #5A5A5A;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 40em) {
  .main__text {
    font-size: 1.4375rem;
  }
}

.main__form {
  width: 73%;
}
.main__input {
  border: 1px solid #818181;
  border-radius: 7px;
  height: 2.3125rem;
  line-height: 2.3125rem;
  margin-bottom: 0.875rem;
  display: block;
  text-align: center;
}
.main__input::placeholder {
  text-align: center;
}
.main__error--uppercase {
  margin-top: -0.625rem;
  color: red;
  display: none;
  font-style: italic;
}
.main__error--length {
  margin-bottom: 0.625rem;
  color: red;
  display: none;
  font-style: italic;
}
.main__error--email {
  margin-bottom: 0.625rem;
  color: red;
  display: none;
  font-style: italic;
}
.main__button {
  background-image: linear-gradient(270deg, #ff713b 0%, #ffa51d 100%);
  border-radius: 7px;
  height: 2.3125rem;
  border: none;
  text-transform: uppercase;
  margin-bottom: 0.875rem;
  font-size: 0.625rem;
  color: #fff;
  letter-spacing: 0.09375rem;
  cursor: pointer;
}
.main__button:hover {
  background-image: linear-gradient(270deg, #ff9f7c 0%, #ffb13c 100%);
}
.main__small {
  color: #989898;
  font-size: 0.9375rem;
}